<template>
  <base-dialog
    v-if="inputIsInvalid"
    title="Invalid Input"
    @close="confirmError"
  >
    <template #default>
      <p>Unfortunately, at least one input value is invalid.</p>
      <p>
        Please check all inputs and make sure at least a few charecters into
        each input fields.
      </p>
    </template>
    <template #actions>
      <base-button @click="confirmError">Okay</base-button>
    </template>
  </base-dialog>
  <base-card>
    <form @submit.prevent="addNewResource">
      <div class="form-control">
        <label for="title">Title</label>
        <input type="text" id="title" name="title" v-model="titleValue" />
      </div>
      <div class="form-control">
        <label for="description">Description</label>
        <textarea
          name="description"
          id="description"
          rows="3"
          v-model="descriptionValue"
        ></textarea>
      </div>
      <div class="form-control">
        <label for="link">Link</label>
        <input type="url" name="link" id="link" v-model="linkValue" />
      </div>
      <div>
        <base-button type="submit">Add Resource</base-button>
      </div>
    </form>
  </base-card>
</template>

<script>
export default {
  inject: ['addResource'],
  data() {
    return {
      inputIsInvalid: false,
      titleValue: '',
      descriptionValue: '',
      linkValue: ''
    };
  },
  methods: {
    addNewResource() {
      const newResource = {
        id: this.titleValue,
        title: this.titleValue,
        description: this.descriptionValue,
        link: this.linkValue
      };
      if (
        this.titleValue.trim() === '' ||
        this.descriptionValue.trim() === '' ||
        this.linkValue.trim() === ''
      ) {
        this.inputIsInvalid = true;
        return;
      }
      this.addResource(newResource);
      console.log(this.titleValue, this.descriptionValue, this.linkValue);
    },
    confirmError() {
      this.inputIsInvalid = false;
    }
  }
};
</script>

<style scoped>
label {
  font-weight: bold;
  display: block;
  margin-bottom: 0.5rem;
}

input,
textarea {
  display: block;
  width: 100%;
  font: inherit;
  padding: 0.15rem;
  border: 1px solid #ccc;
}

input:focus,
textarea:focus {
  outline: none;
  border-color: #3a0061;
  background-color: #f7ebff;
}

.form-control {
  margin: 1rem 0;
}
</style>
